import React, { useState } from 'react';
import { Mutation, Query } from 'react-apollo';
import gql from 'graphql-tag';
import Router from 'next/router';
import Form from './styles/Form';
import formatMoney from '../lib/formatMoney';
import Error from './ErrorMessage';

export const SINGLE_ITEM_QUERY = gql`
  query SINGLE_ITEM_QUERY($id: ID!) {
    item(where: { id: $id }) {
      id
      title
      description
      price
    }
  }
`;

export const UPDATE_ITEM_MUTATION = gql`
  mutation UPDATE_ITEM_MUTATION(
    $title: String!
    $description: String!
    $price: Int!
  ) {
    createItem(
      title: $title
      description: $description
      price: $price
      image: $image
      largeImage: $largeImage
    ) {
      id
      title
      description
      price
    }
  }
`;

const UpdateItem = props => {
  const [title, setTitle] = useState('');
  const [description, setDescription] = useState('');
  const [price, setPrice] = useState(0);

  const updateItem = async (e, updateItemMutation) => {
    e.preventDefault();
    console.log('updating item..');
    const variables = {
      id: props.id,
    };
    title ? variables.title = title : null;
    description ? variables.description = description : null;
    price ? variables.price = price : null;
    const res = await updateItemMutation({ variables });
  }

  return (
    <Query query={SINGLE_ITEM_QUERY} variables={{ id: props.id }}>
      {({ data, loading }) => {
        if (loading) <p>Loading...</p>;
        if (!data.item) return <p>No item for ID {props.id}</p>
        return (
          <Mutation
            mutation={UPDATE_ITEM_MUTATION}
            variables={{
              title,
              description,
              image,
              largeImage,
              price
            }}
          >
            {(updateItem, { loading, error }) => (
              <Form onSubmit={e => updateItem(e, updateItem)} method="post">
                <Error error={error} />
                <fieldset disabled={loading} aria-busy={loading}>
                  <label htmlFor="title">
                    Title
                    <input
                      type="text"
                      id="title"
                      name="title"
                      placeholder="Title"
                      required
                      defaultValue={data.item.title}
                      onChange={e => setTitle(e.target.value)}
                    />
                  </label>
                  <label htmlFor="price">
                    Price
                    <input
                      type="number"
                      id="price"
                      name="price"
                      placeholder="Price"
                      required
                      defaultValue={data.item.price}
                      onChange={e => setPrice(e.target.value)}
                    />
                  </label>
                  <label htmlFor="title">
                    Description
                    <textarea
                      id="description"
                      name="description"
                      placeholder="Enter a Description"
                      required
                      defaultValue={data.item.description}
                      onChange={e => setDescription(e.target.value)}
                    />
                  </label>
                  <button type="submit">Submit</button>
                </fieldset>
              </Form>
            )}
          </Mutation>
        );
      }}
    </Query>
  );
};

export default UpdateItem;
